"use client";

import { Canvas } from "@react-three/fiber";
import { Suspense, useEffect } from "react";
import { Earth } from "./Earth";
import { StationMarkers } from "./StationMarkers";
import { GlobeControls } from "./GlobeControls";
import { Stars } from "@react-three/drei";
import { useGlobeStore } from "../stores/globe-store";
import { radioDiscoveryService } from "../services/radioDiscoveryService";
import { latLngToPosition } from "../utils/coordinates";

export function Globe() {
  const { addMarker } = useGlobeStore();

  // Load radio stations when component mounts
  useEffect(() => {
    const loadStations = async () => {
      try {
        console.log("Loading radio stations...");
        const stations = await radioDiscoveryService.getTopStations(100);
        console.log("Loaded stations:", stations.length);
        
        stations.forEach((station) => {
          console.log("Adding station:", station.name, station.location);
          addMarker(station);
        });
      } catch (error) {
        console.error("Failed to load stations:", error);
      }
    };

    loadStations();
  }, [addMarker]);

  return (
    <Canvas
      camera={{
        position: [0, 0, 2.5],
        fov: 75,
        near: 0.1,
        far: 1000,
      }}
      gl={{
        antialias: true,
        alpha: true,
        powerPreference: "high-performance",
      }}
    >
      <Suspense fallback={null}>
        {/* Dynamic Lighting for Day/Night Cycle */}
        <ambientLight intensity={0.15} />
        <directionalLight
          position={[5, 2, 5]}
          intensity={0.8}
          castShadow
          shadow-mapSize-width={2048}
          shadow-mapSize-height={2048}
          shadow-camera-far={50}
          shadow-camera-left={-10}
          shadow-camera-right={10}
          shadow-camera-top={10}
          shadow-camera-bottom={-10}
        />

        {/* Rim lighting for atmospheric effect */}
        <pointLight position={[-10, -10, -10]} intensity={0.1} />

        {/* Background stars */}
        <Stars
          radius={300}
          depth={60}
          count={20000}
          factor={7}
          saturation={0}
          fade
        />

        {/* Earth */}
        <Earth />

        {/* Radio Station Markers */}
        <StationMarkers />

        {/* Camera Controls */}
        <GlobeControls />
      </Suspense>
    </Canvas>
  );
}
