"use client";

import { useState, useEffect } from "react";
import {
  Search,
  MapPin,
  Volume2,
  Heart,
  Share,
  Globe,
  Music,
  Radio,
} from "lucide-react";
import { motion, AnimatePresence } from "framer-motion";
import { RadioStation } from "../types";
import { useGlobeStore } from "../stores/globe-store";
import { radioStationService } from "../services/radioStationService";

interface SearchPanelProps {
  className?: string;
}

export function SearchPanel({ className = "" }: SearchPanelProps) {
  const [isOpen, setIsOpen] = useState(false);
  const [searchTerm, setSearchTerm] = useState("");
  const [searchResults, setSearchResults] = useState<RadioStation[]>([]);
  const [popularStations, setPopularStations] = useState<RadioStation[]>([]);
  const [isLoading, setIsLoading] = useState(false);
  const [searchFilter, setSearchFilter] = useState<
    "all" | "genre" | "country" | "city"
  >("all");
  const { selectStation } = useGlobeStore();

  // Load popular stations once
  useEffect(() => {
    const fetchPopular = async () => {
      try {
        const stations = await radioStationService.getPopularStations(8);
        setPopularStations(stations);
      } catch (error) {
        console.error("Failed to load popular stations:", error);
      }
    };
    fetchPopular();
  }, []);

  // Enhanced search with filters
  useEffect(() => {
    let timer: NodeJS.Timeout;

    if (searchTerm.length > 2) {
      setIsLoading(true);

      timer = setTimeout(async () => {
        let results: RadioStation[] = [];

        try {
          switch (searchFilter) {
            case "genre":
              results =
                await radioStationService.getStationsByGenre(searchTerm);
              break;
            case "country":
              results =
                await radioStationService.getStationsByCountry(searchTerm);
              break;
            case "city":
              results = await radioStationService.getStationsByCity(searchTerm);
              break;
            default:
              results = await radioStationService.searchStations(searchTerm);
          }

          setSearchResults(results.slice(0, 10)); // Limit results
        } catch (error) {
          console.error("Search error:", error);
          setSearchResults([]);
        } finally {
          setIsLoading(false);
        }
      }, 300);
    } else {
      setSearchResults([]);
      setIsLoading(false);
    }

    return () => {
      if (timer) clearTimeout(timer);
    };
  }, [searchTerm, searchFilter]);

  const handleStationSelect = (station: RadioStation) => {
    selectStation(station);
    setIsOpen(false);
    setSearchTerm("");
    setSearchResults([]);
  };

  return (
    <div className={`relative ${className}`}>
      {/* Search Input */}
      <motion.div className="flex items-center" whileHover={{ scale: 1.02 }}>
        <div className="relative">
          <Search className="absolute left-3 top-3 w-4 h-4 text-gray-400" />
          <input
            type="text"
            value={searchTerm}
            onChange={(e) => setSearchTerm(e.target.value)}
            onFocus={() => setIsOpen(true)}
            placeholder="Search radio stations..."
            className="glass pl-10 pr-4 py-2 w-64 rounded-lg text-white placeholder-gray-400 focus:outline-none focus:ring-2 focus:ring-blue-500"
          />
          {isLoading && (
            <div className="absolute right-3 top-3">
              <div className="w-4 h-4 border-2 border-white border-t-transparent rounded-full animate-spin" />
            </div>
          )}
        </div>
      </motion.div>

      {/* Search Results Dropdown */}
      <AnimatePresence>
        {isOpen && (
          <motion.div
            initial={{ opacity: 0, y: -10 }}
            animate={{ opacity: 1, y: 0 }}
            exit={{ opacity: 0, y: -10 }}
            className="absolute top-full left-0 mt-2 w-80 glass rounded-xl overflow-hidden z-50 max-h-96 overflow-y-auto"
          >
            {/* Search Filters */}
            <div className="p-3 border-b border-white/10">
              <div className="flex gap-2">
                {["all", "genre", "country", "city"].map((filter) => (
                  <button
                    key={filter}
                    onClick={() => setSearchFilter(filter as any)}
                    className={`px-3 py-1 text-xs rounded-full transition-colors ${
                      searchFilter === filter
                        ? "bg-blue-500 text-white"
                        : "bg-white/10 text-gray-300 hover:bg-white/20"
                    }`}
                  >
                    {filter.charAt(0).toUpperCase() + filter.slice(1)}
                  </button>
                ))}
              </div>
            </div>

            {/* Search Results */}
            {searchResults.length > 0 && (
              <div className="p-2">
                <div className="text-xs text-gray-400 px-2 py-1">
                  {searchResults.length} stations found
                </div>
                {searchResults.map((station) => (
                  <motion.button
                    key={station.id}
                    onClick={() => handleStationSelect(station)}
                    className="w-full p-3 text-left hover:bg-white/10 rounded-lg transition-colors"
                    whileHover={{ scale: 1.02 }}
                  >
                    <div className="flex items-center gap-3">
                      <div className="w-10 h-10 bg-gradient-to-r from-blue-500 to-purple-600 rounded-lg flex items-center justify-center flex-shrink-0">
                        <Radio className="w-5 h-5 text-white" />
                      </div>
                      <div className="flex-1 min-w-0">
                        <div className="font-medium text-white text-sm truncate">
                          {station.name}
                        </div>
                        <div className="text-xs text-gray-400 flex items-center gap-2">
                          <MapPin className="w-3 h-3" />
                          {station.location.city}, {station.location.country}
                          <span className="bg-blue-500/20 text-blue-300 px-1.5 py-0.5 rounded text-xs">
                            {station.genre}
                          </span>
                        </div>
                      </div>
                      <div className="text-xs text-gray-500">
                        {(station.listeners / 1000).toFixed(0)}k
                      </div>
                    </div>
                  </motion.button>
                ))}
              </div>
            )}

            {/* Popular Stations (when no search) */}
            {searchTerm.length === 0 && popularStations.length > 0 && (
              <div className="p-2">
                <div className="text-xs text-gray-400 px-2 py-2 flex items-center gap-2">
                  <Music className="w-3 h-3" />
                  Popular Stations
                </div>
                {popularStations.map((station) => (
                  <motion.button
                    key={station.id}
                    onClick={() => handleStationSelect(station)}
                    className="w-full p-3 text-left hover:bg-white/10 rounded-lg transition-colors"
                    whileHover={{ scale: 1.02 }}
                  >
                    <div className="flex items-center gap-3">
                      <div className="w-8 h-8 bg-gradient-to-r from-green-500 to-blue-600 rounded-lg flex items-center justify-center flex-shrink-0">
                        <span className="text-white text-xs font-bold">
                          {station.name.charAt(0)}
                        </span>
                      </div>
                      <div className="flex-1 min-w-0">
                        <div className="font-medium text-white text-sm truncate">
                          {station.name}
                        </div>
                        <div className="text-xs text-gray-400">
                          {station.location.city}, {station.location.country}
                        </div>
                      </div>
                      <div className="text-xs text-green-400">
                        {(station.listeners / 1000).toFixed(0)}k
                      </div>
                    </div>
                  </motion.button>
                ))}
              </div>
            )}

            {/* No Results */}
            {searchTerm.length > 2 &&
              searchResults.length === 0 &&
              !isLoading && (
                <div className="p-6 text-center text-gray-400">
                  <Globe className="w-8 h-8 mx-auto mb-2 opacity-50" />
                  <div className="text-sm">No stations found</div>
                  <div className="text-xs">
                    Try searching by genre or country
                  </div>
                </div>
              )}

            {/* Close Button */}
            <div className="p-2 border-t border-white/10">
              <button
                onClick={() => {
                  setIsOpen(false);
                  setSearchTerm("");
                }}
                className="w-full py-2 text-xs text-gray-400 hover:text-white transition-colors"
              >
                Close
              </button>
            </div>
          </motion.div>
        )}
      </AnimatePresence>

      {/* Click outside to close */}
      {isOpen && (
        <div
          className="fixed inset-0 z-40"
          onClick={() => {
            setIsOpen(false);
            setSearchTerm("");
          }}
        />
      )}
    </div>
  );
}
